/* -----------------------------------------------------------------------------
// 1. Header 
// ----------------------------------------------------------------------------- */
header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  margin: 0 auto;

  z-index: 1;
  padding: 5px 0; }
  header:before, header:after {
    content: " ";
    display: table; }
  header:after {
    clear: both; }
  header nav.sidebar {
    position: absolute;
    top: 0;
    right: 0;
    opacity: 0;
    height: 800px;
    height: 100vh;
    background-color: rgba(29, 79, 144, .85);
    width: 20%;
    padding-left: 40px;
    padding-top: 100px;
    -webkit-transition: opacity 0.5s;
    -moz-transition: opacity 0.5s;
    -ms-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    transition: opacity 0.5s; }
    header nav.sidebar li {
      margin-bottom: 20px; }
      header nav.sidebar li a {
        color: #fff;
        text-transform: uppercase;
        letter-spacing: 2px;
       font-family: 'DINNextLTArabic'; }
    header nav.sidebar .social {
      margin: 60px 0 25px 0; }
      header nav.sidebar .social li {
        display: inline-block;
        margin-right: 10px; }
      header nav.sidebar .social a {
        color: #fff;
        font-size: 14px; }
    header nav.sidebar .btn {
      background-color: #fff;
      color: #11b8d7; }
      header nav.sidebar .btn:hover {
        color: #fff;
        border-color: #fff;
        background-color: transparent; }
  @media (max-width: 992px) {
    header nav.sidebar {
      width: 50%; } }

.sidebar-active {
  opacity: 1 !important; }

/* -----------------------------------------------------------------------------
// 2. Footer 
// ----------------------------------------------------------------------------- */
footer {
  position: relative;
  background: url(../images/img-Contact-Us.jpg) no-repeat center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -ms-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  color: #fff; }
  footer:after {
    content: ' ';
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(29, 79, 144, .85); }
  footer .section-headline h4,
  footer .section-headline p {
    color: #fff !important; }
  footer .footer-main {
    position: relative;
    padding: 100px 0 0 0;
    z-index: 2; }
    footer .footer-main .col {
      text-align: center;
      }
      footer .footer-main .col p {
        margin-bottom: 5px; }
    footer .footer-main p {
      font-family: 'DINNextLTArabic';
    }
    footer .footer-main .icon {
      width: 70px;
      height: 70px;
      line-height: 70px;
      margin: 0 auto 30px auto;
      background-color: #FBB056;
      color: #fff;
      border-radius: 100%;
      text-align: center;
      font-size: 22px; }
    @media (max-width: 992px) {
      footer .footer-main .col {
        margin-bottom: 50px; } }
  footer .footer-social {
    position: relative;
    z-index: 2;
    text-align: center;
    padding: 20px 0;
    margin-top: 40px;
    background-color: #fff;
    background-color: rgba(255, 255, 255, 0.1); }
    footer .footer-social ul {
      padding: 0;
      margin-bottom: 0; }
      footer .footer-social ul li {
        display: inline; }
      footer .footer-social ul a {
        margin: 0 20px;
        font-size: 24px;
        color: #fff;
        opacity: .2;
        -webkit-transition: opacity 0.3s;
        -moz-transition: opacity 0.3s;
        -ms-transition: opacity 0.3s;
        -o-transition: opacity 0.3s;
        transition: opacity 0.3s; }
        footer .footer-social ul a:hover {
          opacity: 1;
          text-decoration: none; }
  footer .footer-copyright {
    position: relative;
    z-index: 2;
    letter-spacing: 2px;
    padding: 25px 0;
    text-align: center;
    font-family: 'DINNextLTArabic';
    text-transform: uppercase;
    color: #fff; }

/* -----------------------------------------------------------------------------
// 3. Hero Title 
// ----------------------------------------------------------------------------- */
.hero-title {
  padding: 200px 0 100px 0;
  background-color: #aeaeae;
  text-align: center;
  background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -ms-background-size: cover;
  -o-background-size: cover;
  background-size: cover; }
  .hero-title h2 {
    padding: 0;
    font-size: 48px;
    color: #1D4F90;
    font-weight: 500; }
    .hero-title h2 small {
      display: block;
      margin: 10px 0 0 0;
      font-family: 'DINNextLTArabic';
      color: #4b4b4b;
      letter-spacing: 2px;
      font-size: 16px;
      text-transform: uppercase;
      color: #181818; }

